.header {
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
}

header:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  z-index: 2;
}

.header_body {
  position: relative;
  height: 80px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.header__logo {
  font-size: 36px;
  font-family: Post No Bills Jaffna SemiBold;
  color: #FFFFFF;
  text-transform: uppercase;
  position: relative;
  z-index: 3;
}

.header_burger {
  display: none;
}

.header_list {
  display: flex;
  position: relative;
  z-index: 2;
}
.header_list li {
  margin-left: 20px;
}

.header_link {
  color: white;
  text-transform: uppercase;
  font-size: 14px;
}

@media (max-width: 767px) {
  body.lock {
    overflow: hidden;
  }
  .header_body {
    height: 50px;
    justify-content: space-between;
    margin: 0 10px;
  }
  .header__logo {
    font-size: 25px;
  }
  .header_burger {
    display: block;
    position: relative;
    width: 30px;
    height: 2px;
    z-index: 3;
  }
  .header_burger span {
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 2px;
    top: 9px;
    left: 0px;
    transition: all 0.3s ease 0s;
  }
  .header_burger:before, .header_burger:after {
    content: "";
    background-color: #fff;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }
  .header_burger:before {
    top: 2px;
  }
  .header_burger:after {
    bottom: 5px;
  }
  .header_burger.active span {
    transform: scale(0);
  }
  .header_burger.active::after {
    transform: rotate(45deg);
    top: 2px;
  }
  .header_burger.active::before {
    transform: rotate(-45deg);
    bottom: 9px;
  }
  .header_link {
    font-size: 20px;
    display: flex;
    justify-content: center;
  }
  .header_menu {
    position: fixed;
    top: -100%;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: gray;
    padding: 70px 0px 20px 10px;
    transition: all 0.3s ease 0s;
    overflow: auto;
  }
  .header_menu.active {
    top: 0;
  }
  .header_list {
    display: block;
  }
  .header_list li {
    margin: 0px 0px 20px 0px;
  }
}
