.header 
{
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 50;
}
header:before
{
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  z-index: 2;
}
.header_container {
}
.header_body 
{
  position: relative; 

  height: 80px;
  display: flex;
  justify-content: space-around; 
  align-items: center;
}

.header__logo 
{
  font-size: 36px;
  font-family: Post No Bills Jaffna SemiBold;
  color: #FFFFFF;
  text-transform: uppercase;
  position: relative;
  z-index: 3;
}
.header_burger {
  display: none;
}
.header__menu {
}

.header_list 
{
display: flex;
position: relative;
z-index: 2;
 li
{
  margin-left: 20px;
 
}
}

.header_link 
{
  color:#FFFF;
  text-transform: uppercase;
  font-size: 14px;
}


@media (max-width:767px) 
{
  body.lock
  {
    overflow: hidden;
  }
.header_body{
  height: 50px;
  justify-content: space-between;
  margin: 0 10px;
}
.header__logo{font-size: 25px;}
.header_burger
{
 display: block;
 position: relative;
 width: 30px;
 height: 2px;
 z-index: 3;
 
}
.header_burger span
 {
    position: absolute;
    background-color: #fff;
    width: 100%;
    height: 2px;
    top: 9px;
    left: 0px;
    transition: all 0.3s ease 0s;
 }

.header_burger:before, .header_burger:after
{
  content: '';
  background-color: #fff;
  position: absolute;
  width: 100%;
  height: 2px;
  left: 0;
  transition: all 0.3s ease 0s;
}
.header_burger:before
{
  top: 2px;
}
.header_burger:after
{
  bottom:5px; 
}
.header_burger.active span
{
  transform: scale(0);
}
.header_burger.active::after
{
transform: rotate(45deg);
top:2px;
}

.header_burger.active::before
{
transform: rotate(-45deg);
bottom: 9px;
}

.header_link
{
  font-size: 20px;
  display: flex;
  justify-content: center;

} 
.header_menu
{
position: fixed;
top: -100%;
left: 0;
width: 100%;
height: 100%;
background-color: gray;
padding: 70px 0px 20px 10px;
transition: all 0.3s ease 0s;
overflow: auto;
}
.header_menu.active
{
  top: 0;
}
.header_list
{
display: block;
li
{
  margin: 0px 0px 20px 0px;
}
}
}











.start_page {
  width: 100%;
  min-height: 810px;
  background: url(/src/img/mainBack.png) 50% no-repeat;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;

  .start_page_block {
    width: 320px;
    height: 320px;
    border: 4px solid rgb(255, 255, 255);

    .start_block_title {
      width: 238px;
      display: inline-block;
      margin-top: 94px;
      margin-left: 40px;
      margin-bottom: 3px;
      text-transform: uppercase;
      color: #ffffff;
      font-family: "Post No Bills Jaffna SemiBold", sans-serif;
      font-weight: 700;
      font-size: 64px;
      font-style: normal;
      text-align: center;
    }

    .start_block_title_line {
      border: 1px solid white;
      width: 183px;
      margin: 0 auto;
      margin-bottom: 7px;
    }

    .start_block_text {
      text-align: center;
      font-size: 22px;
      text-transform: uppercase;
      color: #ffffff;
      font-family: PT Sans;
      font-weight: 400;
      line-height: 28px;
    }
  }

  .start_page_text {
    position: absolute;
    max-width: 730px;
    min-height: 96px;
    margin-top: 550px;
    font-family: PT Sans;
    text-transform: uppercase;
    font-size: 16px;
    color: #ffffff;
    text-align: center;
    line-height: 24px;

    @media (max-width:750px) {
      width: auto;
      margin-left: 10px;
      margin-right: 10px;
    }
  }
}

.main_page {
  display: flex;
  flex-wrap: wrap;

  .about_item_season2020 {
    width: 50%;
    height: 730px;
    background-color: #000000;

    @media (max-width:550px) {
      height: 1500px;
    }
  }

  .block_title {
    margin-top: 93px;
    margin-left: auto;

    .title_h1 {
      text-align: center;
      font-size: 36px;
      text-transform: uppercase;
      font-family: "Playfair Display";
      color: #ffffff;
    }

    .title_line {
      width: 120px;
      margin: 50px auto;
      border: 1px solid #ffffff;
    }
  }

  .products_block_wrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    position: relative;

    .products_block_item {
      width: 25%;
      height: 325px;
      background-color: #191b1c;

      @media (max-width:550px) {
        width: 80%;
        margin-bottom: 80px;
      }

      .item_img {
        display: flex;
        justify-content: center;
        margin-top: 50px;
      }

      &:hover {
        cursor: pointer;
        background: transparent
      }

    }

    .watch_block_text {
      position: absolute;
      margin-top: 80px;

      @media (max-width:550px) {
        margin-top: 80px;
      }

      .watch_block_name {
        font-size: 18px;
        color: #ffffff;
        margin-bottom: 10px;
        line-height: 21px;
      }

      .watch_block_price {
        font-size: 16px;
        color: #ffffff;
        line-height: 19px;
      }

      .watch_block_name,
      .watch_block_price {
        font-family: "Playfair Display";
      }
    }

  }

  .about_item_newCollection {
    width: 50%;
    height: 730px;
    background: url(/src/img/bg_newCollection.png);
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover;


    .block_title {
      margin: 483px auto;

      .title_line {
        margin-top: 30px;
        margin-bottom: 0px;
      }

      .button_wrap_newCollect {
        display: flex;
        justify-content: center;

        .page_info_button {
          margin-top: 30px;
          text-align: center;
          background: transparent;
          font-size: 18px;
          text-transform: uppercase;
          border: 1px solid #ffffff;
          padding: 12px 28px;
          font-family: "PT Sans";

          &:hover {
            cursor: pointer;
            background-color: #1F1F1F;
            font-size: 20px;
          }

          a {
            color: #FFFFFF;
          }
        }
      }

    }
  }

  .about_item_img {
    width: 40%;
    height: 600px;
    background-image: url(/src/img/bg_item_img.png);
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover;


  }

  .about_item_collect2018 {
    width: 60%;
    min-height: 600px;
    background-color: #000000;

    .block_title {
      margin-top: 100px;
    }

    .collect2018_text {
      margin: 0px auto;
      width: 471px;
      min-height: 173px;
      font-size: 14px;
      text-align: center;
      color: white;
      font-family: "Playfair Display";
      line-height: 21px;

      @media (max-width:550px) {
        width: auto;
        margin: 0 15px;
      }
    }

    .button_wrap {
      display: flex;
      justify-content: center;

      .collect2018_button {
        margin-top: 65px;
        text-align: center;
        font-size: 18px;
        text-transform: uppercase;
        border: 1px solid #ffffff;
        padding: 12px 28px;
        color: #ffffff;
        background-color: transparent;
        font-family: "PT Sans";

        &:hover {
          cursor: pointer;
          background-color: #1F1F1F;
          font-size: 20px;
        }

        a {
          color: #FFFF
        }
      }
    }
  }

  .new_progress {
    width: 100%;
    min-height: 1289px;
    background-color: #000000;

    .new_progress_wrap {
      display: flex;
      justify-content: center;
    }

    .products_block_wrap {
      width: 80%;
      height: 962px
    }


    .products_block_item {
      width: 24%;

    }

    @media screen and (max-width:860px) {
      height: 1900px;

      .products_block_item {
        width: 45%;
        margin-bottom: 80px;
      }

    }

    @media screen and (max-width:450px) {
      height: 3500px;

      .products_block_item {
        width: 60%;
        margin-bottom: 80px;
      }

    }
  }

  .our_brands {
    width: 100%;
    min-height: 362px;
    background-color: #0F0F0F;

    .block_title {
      margin-top: 60px;
    }

    .title_line {
      margin-top: 40px;
    }

    .brands_img_wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
    }

    .brands_img_item {
      width: 255;
      height: 94px;
      margin-right: 30px;
      margin-bottom: 80px;
    }
  }
}


.about_us {
  width: 100%;
  min-height: 400px;
  background: url(/src/img/about_us.png);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: auto 100%;

  .footer_wrap {
    width: 100%;
    flex-wrap: wrap;
    min-height: 220px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 80px;

    .footer_item_shop {
      width: 30%;
      height: 219px;
      margin-right: 31px;
    }

    .footer_item_categories {
      width: 15%;
      height: 190px;
      margin-right: 80px;
    }

    .footer_item_mailingList {
      width: 30%;
      height: 200px;
    }

    .footer_item_title {
      font-family: "Playfair Display";
      line-height: 28px;
      font-size: 24px;
      color: #FFFFFF;
      margin-bottom: 40px;
    }

    .footer_item_text {
      font-family: "PT Sans";
      font-size: 14px;
      line-height: 21px;
      color: #FFFFFF;
    }

    .button_wrap {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;


      .button_item {
        font-size: 14px;
        padding: 10px 20px;
        background-color: #000000;
        color: #FFFFFF;
        margin-bottom: 15px;
        text-transform: lowercase;
        font-family: "PT Sans";
      }
    }

    .input_wrap {
      margin-top: 37px;
      display: flex;
      flex-wrap: wrap;

      .input_item_email {
        width: 210px;
        height: 40px;
        background: rgba(255, 255, 255, 0.4);
        font-size: 20px;
        padding: 5px;
        font-family: "PT Sans";
      }

      .input_item_submit {
        padding: 10px 20px;
        font-family: "PT Sans";
        background: #000000;
        color: #FFFFFF;
        line-height: 21px;
        font-size: 14px;
        text-transform: uppercase;

        &:hover {
          cursor: pointer;
        }
      }

      @media (max-width:363px) {
        .input_item_email {
          width: 50%;
        }

        .input_item_submit {
          width: 25%;
        }
      }
    }

    @media (max-width:1650px) {
      .footer_item_categories {
        width: 20%;
        margin-right: 10px;
      }

      .footer_item_mailingList {
        margin-left: 15px;
      }

    }

    @media (max-width:1200px) {
      height: 550px;
      align-items: flex-start;
      padding-top: 25px;

      .footer_item_shop,
      .footer_item_categories,
      .footer_item_mailingList {
        width: 50%;
        height: auto;
        margin-right: 0px;

      }

      .footer_item_categories {
        margin-right: 10px;
      }
    }

    @media (max-width:992px) {
      height: 590px;
      align-items: flex-start;
      padding-top: 25px;

      .footer_item_shop,
      .footer_item_categories,
      .footer_item_mailingList {
        width: 70%;
        height: auto;
        margin-right: 0px;
      }

      .footer_item_categories {
        margin-right: 10px;
      }
    }

    @media (max-width:768px) {

      height: 650px;
      align-items: flex-start;
      padding-top: 25px;

      .footer_item_shop,
      .footer_item_categories,
      .footer_item_mailingList {
        width: 80%;
        height: auto;
        margin-right: 0px;
      }

      .footer_item_categories {
        margin-right: 10px;
      }
    }

    @media (max-width:480px) {
      height: 670px;
      align-items: flex-start;
      padding-top: 25px;

      .footer_item_shop,
      .footer_item_categories,
      .footer_item_mailingList {
        width: 90%;
        height: auto;
        margin-right: 0px;
      }

      .footer_item_categories {
        margin-right: 10px;
      }
    }
  }
}

